___CSS_LOADER_EXPORT___.push([module.id, `.form-field {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
 
.form-field .form-group textarea::-moz-placeholder {
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
}
  .form-field .form-group textarea::placeholder {
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
  }
  .form-field .form-group button {
    padding: 10px 20px;
    background-color: #008DDA;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.206);
    margin-top: 10px;
    transition: 150ms all;
    cursor: pointer;
  }
  .form-field .form-group button:hover {
    transform: translateY(-3px);
  }
  
  h2 {
    margin-bottom: 10px;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 40px;
    margin-top: 20px;
  }
  
  .container-note {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .form-field .form-group {
    width: 90%;
  }
  .form-field .form-group input {
    width: 100%;
    display: block;
    border: none;
    outline: none;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    border: 2px solid grey;
    font-size: 1rem;
  }
  .form-field .form-group input:focus {
    border: 2px solid #008DDA;
  }
  .form-field .form-group input::-moz-placeholder {
    font-size: 1rem;
  }
  .form-field .form-group input::placeholder {
    font-size: 1rem;
  }
  .form-field .form-group textarea {
    width: 100%;
    border-radius: 10px;
    border: none;
    padding: 10px;
    border: 2px solid grey;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
  }
  .form-field .form-group textarea:focus {
    outline: none;
    border: 2px solid #008DDA;
    box-sizing: border-box;
  }`, "",{"version":3,"sources":["webpack://./src/styles/form.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;EAClB;;AAEF;IACI,eAAe;IACf,yCAAyC;AAC7C;EACE;IACE,eAAe;IACf,yCAAyC;EAC3C;EACA;IACE,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;IACZ,iBAAiB;IACjB,YAAY;IACZ,mBAAmB;IACnB,6CAA6C;IAC7C,gBAAgB;IAChB,qBAAqB;IACrB,eAAe;EACjB;EACA;IACE,2BAA2B;EAC7B;;EAEA;IACE,mBAAmB;IACnB,yCAAyC;IACzC,iBAAiB;IACjB,gBAAgB;EAClB;;EAEA;IACE,UAAU;IACV,YAAY;IACZ,aAAa;IACb,0BAA0B;IAC1B,SAAS;EACX;;EAEA;IACE,UAAU;EACZ;EACA;IACE,WAAW;IACX,cAAc;IACd,YAAY;IACZ,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,sBAAsB;IACtB,eAAe;EACjB;EACA;IACE,yBAAyB;EAC3B;EACA;IACE,eAAe;EACjB;EACA;IACE,eAAe;EACjB;EACA;IACE,WAAW;IACX,mBAAmB;IACnB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,eAAe;IACf,yCAAyC;EAC3C;EACA;IACE,aAAa;IACb,yBAAyB;IACzB,sBAAsB;EACxB","sourcesContent":[".form-field {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    margin-top: 20px;\n  }\n \n.form-field .form-group textarea::-moz-placeholder {\n    font-size: 1rem;\n    font-family: Arial, Helvetica, sans-serif;\n}\n  .form-field .form-group textarea::placeholder {\n    font-size: 1rem;\n    font-family: Arial, Helvetica, sans-serif;\n  }\n  .form-field .form-group button {\n    padding: 10px 20px;\n    background-color: #008DDA;\n    color: white;\n    font-weight: bold;\n    border: none;\n    border-radius: 10px;\n    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.206);\n    margin-top: 10px;\n    transition: 150ms all;\n    cursor: pointer;\n  }\n  .form-field .form-group button:hover {\n    transform: translateY(-3px);\n  }\n  \n  h2 {\n    margin-bottom: 10px;\n    font-family: Arial, Helvetica, sans-serif;\n    margin-left: 40px;\n    margin-top: 20px;\n  }\n  \n  .container-note {\n    width: 90%;\n    margin: auto;\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 30px;\n  }\n  \n  .form-field .form-group {\n    width: 90%;\n  }\n  .form-field